import FileSaver from 'file-saver';

/**
 * delay() 
 *
 * @param timeout  ms
 * @return {Promise<any>}
 */
function delay(timeout) {
  return new Promise(resolve => setTimeout(resolve, timeout));
}

/**
 * checkImageUrlIsValid() url
 *
 * @param url  url
 * @return {Promise<any>}
 */
function checkImageUrlIsValid(url) {
  return new Promise(function(resolve, reject) {
    const img = new Image();
    img.onload = function() {
      resolve(url);
    };

    img.onerror = function(){
      reject('The url of image is invalid.');
    };

    img.src = url;
  });
}


/**
 * getAllDataFromDatabase()  
 *
 * @param db  dbpouchDB
 * @return {Promise<*>}
 */
async function getAllDataFromDatabase(db) {
  try {
    const data = await db.allDocs({include_docs: true});

    let result = {};

    data.rows.map((item) => result[item.id] = item.doc.state);

    // console.log(result);
    // return result;
    return data;
  } catch (e) {
    console.log(e);

    return undefined;
  }
}

/**
 * exportFile()  FileSaver.js 
 *
 * FileSaver.js 
 *
 * @param data   blob 
 * @param type   MIME 
 * @param fileName  
 */
function exportFile(data, type, fileName) {
  const blob = new Blob([data], {type: type});
  FileSaver.saveAs(blob, fileName);
}

/**
 * generateStyledHTML()  .html  exportFile() 
 *
 * @param title  html 
 * @param content  markdown
 */
function generateStyledHTML(title, content) {
  const html =
    `
    <html>
      <header>
        <title>${title}</title>
        <meta name="viewport" content="width=device-width, initial-scale=1">
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/github-markdown-css/2.10.0/github-markdown.min.css">
        <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/prism-themes@1.0.1/themes/prism-darcula.css">
        <style>
          .emoji {
            height: 1.2em;
          }
          
          /*
          github markdown style
          */
          
          .markdown-body {
            box-sizing: border-box;
            min-width: 200px;
            max-width: 980px;
            margin: 0 auto;
            padding: 45px;
          }
        
          @media (max-width: 767px) {
            .markdown-body {
              padding: 15px;
            }
          }
          
          /*
          some optimization to the code block.
          */
          
          .token.keyword {
            font-weight: 900;
          }
          
          .token.inserted {
            color: #50fa7b;
            background: transparent;
          }

          .token.deleted {
            color: #ff5555;
            background: transparent;
          }

        </style>
      </header>
      <body>
        <article class="markdown-body">
          ${content}
        </article>
      </body>
    </html>
  `;

  return html;
}

export {
  delay,
  checkImageUrlIsValid,
  getAllDataFromDatabase,
  exportFile,
  generateStyledHTML
}